import Peer from "peerjs";

const pressFeedbackURI =
  "data:audio/wav;base64,";
// "data:audio/mpeg;base64,";

// -- Parameters --
export interface Message {
  messageType: string;
  keypadId: string,
  receiverId: string,
  sendTime: number,
  pressToSendDuration: number,
  response?: string,
  alphabet?: string[],
  font?: string,
  headerContent?: string,
}

export interface KeypadParameters {
  targetElementId?: string,
  keypadUrl?: string,
  alphabet?: string[],
  font?: string,
  visualResponseFeedback?: boolean,
}

export interface URLParameters {
  peerID: string,
}

export class KeypadPeer {
  peer: Peer;
  conn: Peer.DataConnection;
  lastPeerId: string;
  keypadUrl: string;
  targetElement: string;
  alphabet: string[];
  font: string;
  keypressFeedbackSound: string;
  constructor(
    parameters: KeypadParameters = {
      keypadUrl: "https://www.keypad.website/keypad?",
      targetElementId: null,
    }
  ) {
    /* Create the Peer object for our end of the connection. */
    this.peer = new Peer(null, { debug: 2 });
    this.conn = null;
    this.lastPeerId = null;
    this.keypadUrl = parameters.hasOwnProperty("keypadUrl")
      ? parameters.keypadUrl
      : "https://www.keypad.website/keypad?";
    this.targetElement = parameters.hasOwnProperty("targetElementId")
      ? parameters.targetElementId
      : null;

    this.alphabet = null;
    this.font = null;
    this.keypressFeedbackSound = pressFeedbackURI;
    // "data:audio/mpeg;base64,";
  }

  displayUpdate = (message: string, append: boolean = false) => {
    // If the specified elem exists, update that elem
    if (!!document.getElementById(this.targetElement)) {
      const displayElement = document.getElementById(this.targetElement);
      if (append) {
        const oldInnerText = displayElement.innerText;
        displayElement.innerText = message + "\n" + oldInnerText;
      } else {
        displayElement.innerText = message;
      }
    } else {
      console.log("MESSAGE: ", message);
    }
  };
  onPeerDisconnected = () => {
    this.displayUpdate("Connection lost. Please reconnect");
    // Workaround for peer.reconnect deleting previous id
    this.peer.id = this.lastPeerId;
    // this.peer._lastServerId = this.lastPeerId
    this.peer.reconnect();
  };
  onPeerClose = () => {
    this.displayUpdate("Connection closed");
    this.conn = null;
  };
  onPeerError = (error: Error) => {
    this.displayUpdate(String(error));
    alert("" + String(error));
  };
  parseParams = (params: URLSearchParams) => {
    const font = params.get("font");
    const alphabet = this.checkAlphabet(
      decodeURIComponent(params.get("alphabet")).split(",")
    );
    const peerId = params.get("peerID");
    return { alphabet: alphabet, font: font, peerId: peerId };
  };
  queryStringFromObject = (params: URLParameters) => {
    return Object.entries(params)
      .map(([key, value]) => key + "=" + encodeURIComponent(value))
      .join("&");
  };
  checkAlphabet = (proposedAlphabet: string | string[]) => {
    let validAlphabet: string[];
    let uniqueValidAlphabet: string[];
    if (Array.isArray(proposedAlphabet)) {
      // ARRAY : good
      // FUTURE verify that symbols are displayable in desired font
      validAlphabet = proposedAlphabet;
    } else if (typeof proposedAlphabet == "string") {
      // STRING : ok
      if (
        proposedAlphabet.toUpperCase() === "SPACE" ||
        proposedAlphabet.toUpperCase() == "ESC"
      ) {
        validAlphabet = [proposedAlphabet];
      } else {
        validAlphabet = proposedAlphabet.split("");
      }
    } else {
      // SOMETHING ELSE : bad
      console.error(
        "Error! Alphabet must be specified as an array of symbols, including 'ESC', 'SPACE'"
      );
      validAlphabet = [];
    }
    uniqueValidAlphabet = [...new Set(validAlphabet)];

    // Order alphabet so that if 'SPACE' and 'ESC' are in the list, they are correctly positioned
    if ("SPACE" in uniqueValidAlphabet) {
      uniqueValidAlphabet = moveElementToEndOfArray(
        uniqueValidAlphabet,
        "space"
      );
    }
    if ("ESC" in uniqueValidAlphabet) {
      uniqueValidAlphabet = moveElementToEndOfArray(uniqueValidAlphabet, "ESC");
    }
    return uniqueValidAlphabet;
  };
}

const moveElementToEndOfArray = <T extends unknown>(array:T[], element: T) => {
  return [
    ...array.slice(0, array.indexOf(element)),
    ...array.slice(array.indexOf(element) + 1),
    element,
  ];
};
